import React, { useContext, useState } from "react";
import UserContext from "./context";

function Edituser() {
    let users = useContext(UserContext);
    let [updatUserName, setUpdateUserName] = useState("");
    let [updateUserEmail, setUpdateUserEmail] = useState("");
    let [updateUserOffice, setUpdateUserOffice] = useState("");
    let [updateUserAge, setUpdateUserAge] = useState("");
    let [updateUserAddress, setUpdateUserAddress] = useState("");
    
    return (
        <>
            <h1>Edit User</h1>
            {
                users.userList.map((user) => {
                    return user.userName === users.editUser ? <form onSubmit={(e) => {
                        e.preventDefault();
                        user.userName = updatUserName;
                        user.userEmail = updateUserEmail;
                        user.userOffice = updateUserOffice;
                        user.userAge = updateUserAge;
                        user.userAddress = updateUserAddress;
                    }}>
                        <div className="container">
                            <div className="row">
                                <div className="col-lg-6">
                                    Username<br></br>
                                    <input className="form-control" type="text"  placeholder={user.userName} onChange={(e) => {
                                        setUpdateUserName(e.target.value);
                                    }}></input><br></br>
                                    Email<br></br>
                                    <input className="form-control" type="text"  placeholder={user.userEmail} onChange={(e) => {
                                        setUpdateUserEmail(e.target.value);
                                    }}></input><br></br>
                                    <input className="form-control" type="text"  placeholder={user.userOffice} onChange={(e) => {
                                        setUpdateUserOffice(e.target.value);
                                    }}></input><br></br>
                                    <input className="form-control" type="text"  placeholder={user.userAge} onChange={(e) => {
                                        setUpdateUserAge(e.target.value);
                                    }}></input><br></br>
                                    <input className="form-control" type="text"  placeholder={user.userAddress} onChange={(e) => {
                                        setUpdateUserAddress(e.target.value);
                                    }}></input><br></br>
                                    <button className="btn btn-primary">Update</button>
                                </div>
                            </div>
                        </div>
                    </form>:
                    <p></p>
                })
            }
        </>
    );
}

export default Edituser;