import React, { useContext } from "react";
import { Link } from "react-router-dom";
import UserContext from "./context"

function Listusers() {
    let users = useContext(UserContext);
    return (
        <>
            <h1>List Users</h1>
            <div class="container-fluid">
                <div class="card shadow mb-4">
                    <div class="card-header py-3">
                        <span class="m-0 font-weight-bold text-primary">DataTables Example</span>
                        <Link to="/create-user"><button className="m-0 btn btn-primary float-right">Create</button></Link>
                    </div>
                    <div class="card-body">
                        <div class="table-responsive">
                            <table class="table table-bordered" id="dataTable" width="100%" cellspacing="0">
                                <thead>
                                    <tr>
                                        <th>UserName</th>
                                        <th>Email</th>
                                        <th>Office</th>
                                        <th>Age</th>
                                        <th>Address</th>
                                        <th>Action</th>
                                    </tr>
                                </thead>
                                <tfoot>
                                    <tr>
                                        <th>UserName</th>
                                        <th>Email</th>
                                        <th>Office</th>
                                        <th>Age</th>
                                        <th>Address</th>
                                        <th>Action</th>
                                    </tr>
                                </tfoot>
                                <tbody>
                                    {
                                        users.userList.map((user) => {
                                            return (
                                                <tr>
                                                    <td><Link to="/">{user.userName}</Link></td>
                                                    <td>{user.userEmail}</td>
                                                    <td>{user.userOffice}</td>
                                                    <td>{user.userAge}</td>
                                                    <td>{user.userAddress}</td>
                                                    <td><Link to={`/edit-user/${user.userName}`}><span class="btn btn-primary" onClick={()=>{ users.editUser=user.userName}}>Edit</span></Link></td>
                                                </tr>
                                            )
                                        })
                                    }
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
}

export default Listusers;